<template>
  <div style="width: 1000px;margin: 0 auto;padding-top: 80px;">
    <div class="content">
      <div class="left">
        <img :src="item.avatar_url" alt="" :title="item.html_url" @click="gotogithub(item.html_url)">
      </div>
      <div class="right">
        <div class="title">{{item.title}}</div>
        <div class="infoma">{{item.updatetime}} ✦ {{item.name}} ✦ {{item.email}}</div>
        <div class="con" v-html="item.content"></div>
      </div>
    </div>
  </div>
</template>

<script>
  import {
    mapState,
    mapMutations
  } from 'vuex';

  export default {
    name: "name",
    data() {
      return {
        item: []
      };
    },
    mounted() {
      this.call('/getnewsinfo', {
        nid: localStorage.getItem('nid')
      }, (rs) => {
        if (rs) {
          this.item = rs[0]
        }
      })
    }
  };

</script>

<style scoped>
  .con {
    padding: 6px;
    font-size: 14px;
  }

  .infoma {
    font-size: 13px;
    padding: 3px 6px;
    border-bottom: 1px solid #555;
    color: #888;
    font-weight: 400;
  }

  .title {
    font-size: 20px;
    padding: 6px;
  }

  .left img:hover {
    transform: rotate(360deg)
  }

  .left img {
    width: 50px;
    height: 50px;
    border-radius: 50px;
    margin-left: 10px;
    margin-top: 10px;
    overflow: hidden;
    cursor: pointer;
    transition: 0.2s;
  }

  .right {
    overflow: hidden;
    position: relative;
  }

  .left {
    float: left;
    width: 70px;
    height: 100%;
    overflow: hidden;
  }

  .content {
    overflow: hidden;
    background: #fff;
    margin-bottom: 15px;
    border-radius: 2px;
    font-size: 15px;
    font-weight: 300;
    box-shadow: 0px 1px 10px #ccc;
  }

</style>



