<template>
  <router-view/>
</template>

<script>
  export default {
    name: 'app'
  }

</script>

<style>
  body,
  html {
    width: 100%;
    height: 100%;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    /* background: url('./assets/TIM截图20180116105356.jpg') no-repeat; */
    background: #eee;
    background-size: cover;
    background-attachment: fixed;
    transition: 0.3s;
  }

  * {
    margin: 0px;
    padding: 0px;
  }

  .fade-enter-active,
  .fade-leave-active {
    transition: opacity .5s;
  }

  .fade-enter,
  .fade-leave-to
  /* .fade-leave-active below version 2.1.8 */

  {
    opacity: 0;
  }

  .closebtn:hover {
    background: #3eb8ce;
  }

  .closebtn {
    position: absolute;
    width: 25px;
    height: 25px;
    background: #1d92a6;
    text-align: center;
    line-height: 18px;
    border-radius: 25px;
    color: #fff;
    border: 2px solid #fff;
    right: -13px;
    top: -13px;
    transition: 0.2s;
    cursor: pointer;
  }

  .back {
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0px;
    top: 0px;
    background: rgba(0, 0, 0, 0.5);
    z-index: 999;
  }

  .bg2 {
    background: url('./assets/bg16.png') no-repeat !important;
  }

  div {
    box-sizing: border-box;
    word-break: break-all;
    word-wrap: break-word;
  }

  *::-webkit-scrollbar {
    width: 10px;
    background-color: #eee;
  }


  *::-webkit-scrollbar-thumb {
    background-color: rgba(153, 153, 153, 0.4);
  }

  @font-face {
    font-family: qwe;
    src: url('./assets/Hyperspace.ttf');
  }

  .btn:active {
    color: #ccc;
  }

  .btn:hover {
    background: #2aafc7;
  }

  .btn {
    width: 100px;
    background: #1d92a6;
    height: 30px;
    line-height: 30px;
    text-align: center;
    color: #fff;
    margin: 10px 0px;
    float: left;
    border-radius: 2px;
    transition: 0.2s;
    cursor: pointer;
    user-select: none;
  }

</style>



